  align-items: end;
  max-height: 5px;
  margin:0 auto;
}

.btnDropdown {
  flex: 1 1 auto;
  position: relative;
  display: flex;
  flex-direction: column-reverse;
  align-items: center;
  justify-content: center;
  cursor: pointer;
}

.btnNav {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  grid-gap: 2px;
  gap: 2px;
  height: 30px;
  width: 100%;
  padding: 0 16px;
  font-weight: bold;
  border: none;
  cursor: pointer;
  color: white!important;
}

.btnNav .chevron {
  font-size: 8px;
  opacity: 0.2;
  transition: all 340ms;
  color: black!important;
}

.dropdown {
  display: grid;
  width: calc(100% - 8px);
  grid-template-rows: 0fr;
  background: #f6f6f6;
  border-left: 1px solid #e4e4e4;
  border-right: 1px solid #e4e4e4;
  border-top: 1px solid #e4e4e4;
  border-radius: 16px 16px 0 0;
  opacity: 0;
  transition: all 340ms;
}

.dropdown > .dropdown-contenty {
  overflow: hidden;
  opacity: 0;
  transition: all 340ms;
}

.btnDropdown.first .btnNav { 
  border-radius: 8px 0 0 8px; 
  background: #022d36;
}

.btnDropdown.two .btnNav { 
  background: #ff9333;
}

.btnDropdown.three .btnNav { 
  border-radius: 0 8px 8px 0; 
  background: #ff3633;
}

.btnDropdown:hover .dropdown {
  grid-template-rows: 1fr;
  opacity: 1;
}

.btnDropdown:hover .chevron {
  transform: rotate(180deg);
  transition: all 340ms;
  opacity: 1;
}

.btnDropdown:hover .dropdown .dropdown-contenty {
  opacity: 1;
  transition: all 1s;
}

.dropdown-contenty ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

.dropdown-contenty li {
  padding: 6px 8px;
  font-size: 13px;
  border-bottom: 1px solid #d7d7d7;
}
