*{
    margin: 0;
    padding: 0;
    font-family: "Poppins", sans-serif;
}

:root{
    --pinkish-red: #C35A74;
    --medium-blue: #ff3633;
    --greenish-blue: #022d36;
    --bright-orange: #ff9333;
    --white-smoke: #F5F5F4;
    --white: #FFF;
    --dark-gray: #7D7C7C;
    --black: #000;
}



.content-jvw{
    display: flex;
   
    justify-content: center;
    align-content: center;
   
}

.box-jvw{
    display: flex;
    flex-direction: column;
    height: 486px;
    width: 250px;
    margin-left: 20px;
    border-radius: 20px;
    background: var(--white);
    box-shadow: 0 1rem 2rem rgba(0, 0, 0, 20%);
   
}

.title-jvw{
    width: 100%;
    padding: 10px 0;
    font-size: 1.2em;
    font-weight: lighter;
    text-align: center;
    border-top-left-radius: 20px;
    border-top-right-radius: 20px;
    color: var(--white-smoke);
}

.basic .title-jvw{
    background: var(--bright-orange);
}

.standard .title-jvw{
    background: var(--medium-blue);
}

.business .title-jvw{
    background: var(--greenish-blue);
}

.view{
    display: block;
    width: 100%;
    padding: 30px 0 20px;

    background: var(--white-smoke);
}

.icon-jvw{
    display: flex;
    justify-content: center;
}

.icon-jvw img{
    width: 40px;
}

.cost{
    display: flex;
    justify-content:center;
    flex-direction: row;
    margin-top: 10px;
    font-size: 10px;
}

.amount{
    font-size: 2.8em;
    font-weight: bolder;
}

.detail{
    margin: auto 0 auto 5px;
    width: 70px;
    font-size: 0.7em;
    font-weight: bold;
    line-height: 15px;
    color: #7D7C7C;
}

.description{
    margin: 10px auto;
    font-size: 0.8em;
    color: #7D7C7C;
}

ul{
    list-style: none;
}

li{
    margin-top: 10px;
}

.description li::before{
    content: "";
    background-image: url("https://i.postimg.cc/ht7g996V/check.png");
    background-position: center;
    background-size: cover;
    opacity: 0.5;

    display: inline-block;
    width: 10px;
    height: 10px;
    margin-right: 10px;
}

.button-jv{
    margin: 0 auto 30px;
}

.button-jv button{
    height: 40px;
    width: 150px;
    font-size: 0.7em;
    font-weight: bold;
    letter-spacing: 0.5px;
    color: #7D7C7C;
    border: 2px solid var(--dark-gray);
    border-radius: 50px;

    background: transparent;
}

.txt{
  text-align: center;
  margin-top: 10px;
}
.button-jv button:hover{
    color: var(--white-smoke);
    transition: 0.5s;
    border: none;

    background: var(--bright-orange);
}

